.t-wrapper{
    padding: 0 3rem 0 3rem;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: relative;
    gap: 2rem;
    margin-top: 7rem;
   
}
.t-heading{
    align-self: start;
}


@media screen and (max-width:480px) {
    .t-wrapper{
        padding: 0;
    }
    .testimonial img{
        width: 70px;
        height: 75px;
    }
    
}

.t-heading *{
    font-size: 2rem;
    font-weight: bold;

}
.t-heading span:nth-of-type(2){
    color: orange;

}
.t-blur1{
    left:38rem;
    top:16rem;
}
.t-blur2{
    top: 9rem;
    left: 0;
}
.testimonial{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    padding: 2rem;
    width: 38rem;
    height:12rem;
   
    background: rgba(255,255 , 255, 0.25);
    border: 7px solid rgb(188, 188, 225);
    border-radius: 20px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}

.testimonial img{
    width: 7rem;
    height: 6rem;
    border-radius: 100%;
}
.testimonial span{
    color: gray;
}

/* slider */

.t-wrapper .swiper{
    width: 100%;
    height: 70%;
}



.t-wrapper .swiper-slide{
    display: flex;
    align-items: center;
    justify-content: center;
   
}

.t-wrapper .swiper-pagination-bullet-active{
    background: orange;
}