.n-wrapper{
    height: 10vh;
    display: flex;
   
    justify-content: space-between;

}
.n-left{
    flex:1;
    align-items: center;
    display: flex;
    gap: 2rem;

}
.n-name{
    font-size: 1.6rem;
    font-weight: bold;
}
.n-right{

        display: flex;
        flex: 1;
        align-items: center;
        justify-content: center;
        font-weight: 400;
}
.n-list{
    flex: 10;
}

.n-list>ul{
    display: flex;
    gap: 2rem;
    margin-right: 4rem;
    cursor: pointer;
    list-style: none;
}
.n-list>ul>li :hover{
    color: rgb(249, 194, 13);
}

.n-list>ul>li:hover{
    color: rgb(249, 194, 13);
    cursor: pointer;
}
.button:hover{
    cursor: pointer;
    background-color: aliceblue;
    color: orange;
    border:  1px  solid orange;
   
}


@media screen and (max-width:480px){

.n-list{
    display: none;

}
.n-button{
    display: none;
}

}