.portfolio{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 70vh;


}


/* heading */


.portfolio span:nth-of-type(1){
    color: black;
    font-size: 2rem;
    font-weight: bold;

}

.portfolio span:nth-of-type(2){
    color: orange;
    font-size: 2.5rem;
    font-weight: bolder;

}

/* swiper */
.portfolio-slider{
    margin-top: 3rem;
    width: 100%;
}

.portfolio .swiper{
    overflow: visible!important;
}

.portfolio-slider .swiper-slider{
    width: 20rem;
}

.portfolio img{
    width: 24rem;
    filter: drop-shadow(-12px 15px 13px rgba(0,0,0,0.25)) ;
    border-radius: 19px;

}